<script setup lang="ts">
import { useVueRouter } from "@assets/useHook";

defineOptions({
  name: "payForm",
});

const { routeQuery } = useVueRouter();
const handlePayForm = () => {
  const div: HTMLElement = document.createElement("div");
  div.setAttribute("id", "payContainer");
  div.innerHTML = routeQuery.value.payForm;
  document.body.appendChild(div);
  const payContainer: any = document.querySelector("#payContainer");
  const submit = payContainer.querySelector(
    'form[name="punchout_form"] input[type="submit"]'
  );
  submit.click();
  setTimeout(function () {
    div.remove();
  }, 500);
};
handlePayForm();
</script>
